import React, {useEffect} from "react";
import store from '../Redux/store'
import {Show, Hide} from '../Redux/ReduxAction/isShow'

export default function App(props) {

    useEffect(()=>{ //  Tabbar 
        store.dispatch(Hide())
        return () =>{
            console.log('Center')
            store.dispatch(Show())
        }
    },[])

    return (
        <div>
            Center
        </div>
    )
}