import React, {Component} from 'react';
import store from '../../Redux/store'
import '../../css/antd.css'


function City(props) {
    let list = ["", "", "", "", ""]

    return (
        <div className='cityDiv'>
            <h3></h3>
            <ul className='cityList'>
                {
                  list.map(item =>
                      <li key={item} onClick={()=>{
                          store.dispatch({
                              type: "changeCity",
                              payload: item
                          })

                          props.history.goBack()
                      }}>{item}</li>
                  )
                }
            </ul>
        </div>
    );
}

export default City;